ReactAudioPlayer {
  background-color: aquamarine;
}

.progress-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 20px;

  &__link {
    font-size: 24px;
    color: #01252A;
    padding: 10px 60px;
    background-color: #FFA900;
    border-radius: 10px;
    
    &:hover {
      background-color: #FECC01;
    }
    
    &:active {
      background-color: #FEE35F
    }
  }

  &__title {
    font-size: 36px;
    color: #01252A;
  }

  &__progress {
    &__level {
      &__text {
        font-size: 24px;
        color: #01252A;
      }
      
      &__container {
        position: relative;
        border-radius: 10px;

        &__empty {
          width: 100%;
          height: 7px;
          background-color: #FEE35F;
        }

        &__fill {
          background-color: #01252A;
          height: 7px;
          position: absolute;
          top: 0;
          left: 0;

          @for $end from 1 through 100 {
            &--#{$end} {
              width: calc(#{$end} * 1%);
            }
          }
          
        }
      }
    }

    &__volume {
      &__text {
        font-size: 24px;
        color: #01252A;
      }
      &__container {
        position: relative;
        border-radius: 10px;

        &__empty {
          width: 100%;
          height: 7px;
          background-color: #FEE35F;
        }

        &__fill {
          background-color: #01252A;
          height: 7px;
          position: absolute;
          top: 0;
          left: 0;

          @for $end from 1 through 100 {
            &--#{$end} {
              width: calc(#{$end} * 1%);
            }
          }
          
        }
      }
    }
  }
}

.button {
  font-size: 24px;
  color: #01252A;
  padding: 10px 60px;
  background-color: #FFA900;
  border-radius: 10px;
  
  &:hover {
    background-color: #FECC01;
  }
  
  &:active {
    background-color: #FEE35F
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  &_section {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;

    &__question {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      gap: 20px;

      @media (max-width: 1000px) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
      }

      &__answers {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 20px;

        @media (max-width: 390px) {
          flex-direction: column;
          align-items: flex-start;
          justify-content: center;
          gap: 10px;
        }
      }

      &__wrong {
        background-color: rgb(245, 187, 187);
        border-radius: 30px 0 0 30px;
      }

      &__right {
        background-color: rgb(195, 241, 195);
        border-radius: 30px 0 0 30px;
      }
    }
  }
}

