.section__tests-card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;

  margin-top: 30px;
  margin-bottom: 40px;
  padding-bottom: 30px;
}

.section__test-card {
  height: 400px;
  width: 300px;
  box-sizing: border-box;
  padding: 10px;

  border-radius: 0 0 50px 0;
  border: solid 2px #FFA900;

  &__title {
    font-size: 30px;
    color: #01252A;
  }

  &__description {
    font-size: 20px;
    color: #01252A;
    width: 85%;
    margin-bottom: 25px;
  }
  
  &__link {
    font-size: 24px;
    color: #01252A;
    padding: 10px 30px;
    background-color: #FFA900;
    border-radius: 10px;
    
    &:hover {
      background-color: #FECC01;
    }
    
    &:active {
      background-color: #FEE35F
    }
  }
}
