.section {
  height: 800px;

  &--actual {
    height: 400px;
  }

  &--adv {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  &--about.section--main {
    background-color: #FECC01;
    background-image: url('../../images/c-d-x-PDX_a_82obo-unsplash.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-origin: border-box;
    background-position: center;
  }

  &__title {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 46px;
    color: #01252A;
    text-align: center;

    &--main {
      font-size: 52px;
      text-align: left;
    }
  }

  &__text {
    font-size: 26px;
    color: #01252A;

    &--left {
      width: 400px;
    }
  }

  &__image {
    width: 500px;
  }


  &__tests {
    margin-top: 40px;
    padding-bottom: 20px;
    margin-bottom: 20px;

    &__link {
      width: 50%;
      font-size: 24px;
      color: #01252A;
      padding: 10px 60px;
      background-color: #FFA900;
      border-radius: 10px;
      display: block;
      text-align: center;
      margin: 5px auto;
      
      &:hover {
        background-color: #FECC01;
      }
      
      &:active {
        background-color: #FEE35F
      }
    }
  }

  &__test {
    padding: 40px;
    box-sizing: border-box;
    border: solid 2px #FFA900;
    margin-bottom: 25px;
    border-radius: 0 0 50px 0;

    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('../../images/kelly-sikkema-0vyfyYBPYJ4-unsplash.jpg');
    background-position: right center;

    &__title {
      margin-bottom: 20px;
      font-size: 36px;
      color: #01252A;
    }
  
    &__description {
      font-size: 24px;
      color: #01252A;
      width: 85%;
      margin-bottom: 25px;
    }
    
    &__link {
      font-size: 24px;
      color: #01252A;
      padding: 10px 60px;
      background-color: #FFA900;
      border-radius: 10px;
      
      &:hover {
        background-color: #FECC01;
      }
      
      &:active {
        background-color: #FEE35F
      }
    }
  }
}
